@font-face {
  font-family: marianne;
  font-weight: 400;
  font-style: normal;

  src: url('./fonts/marianne/Marianne-Regular.woff2') format('woff2');
}

@font-face {
  font-family: marianne;
  font-weight: 200;
  font-style: normal;

  src: url('./fonts/marianne/Marianne-Light.woff2') format('woff2');
}

@font-face {
  font-family: marianne;
  font-weight: 500;
  font-style: normal;

  src: url('./fonts/marianne/Marianne-Bold.woff2') format('woff2');
}

html {
  height: 100%;
}

body {
  font-family: "marianne",-apple-system, BlinkMacSystemFont, avenir next, avenir, helvetica neue, helvetica, Ubuntu, roboto, noto, segoe ui, arial, sans-serif;
  font-weight: 700;
  margin: 0;
  padding: 0;
  min-width: 100%;
  min-height: 100%;
  box-shadow: inset 0px 0px 56px rgba(0,0,0,0.3);
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  background-size: 100% auto;
}

body:not([class*="mode"]) {
  opacity: 0;
  visibility: hidden;
}
body[class*="mode"] {
  opacity: 1;
  visibility: visible;
}

h1,
h2,
h3,
p {
  margin: 0 0 25px;
  white-space: pre-wrap;
}

.slide-container:not(:first-child) h2 {
  white-space: nowrap;
}

em {
  font-style: normal;
}

.nowrap {
  white-space: nowrap;
}

* {
  box-sizing: border-box;
}

div.slide-container {
  background-size: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

div.slide {
  cursor: pointer;
  cursor: hand;
}

div.imageText {
  text-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
}

/* normally not good, but ok in context of full screen directional navigation */
.talk-mode .slide:focus {
  outline: 0;
}

body.talk-mode {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
}

body.print-mode {
  background: #fff;
  color: #000;
  font-weight: normal;
  padding: 0px;
}

body.print-mode div.sub-container {
  padding: 10px 20px;
  display: flex;
  flex-flow: row wrap;
  break-inside: avoid;
}

ul.notes-list {
  display: inline-block;
  float: left;
  vertical-align: top;
}

body.print-mode div.slide-container {
  border: 1px solid #000;
}

body.jump-mode {
  padding: 10px;
}

body.jump-mode div.sub-container {
  display: inline-block;
  vertical-align: top;
  padding: 2px;
}

body.jump-mode div.slide-container {
  border: 1px solid rgba(255, 255, 255, 0.2);
  cursor: pointer;
}

body.jump-mode div.slide-container:hover {
  border: 1px solid rgba(255, 255, 255, 0.8);
}

.layout {
  display: grid;
}

.layout div {
  display: flex;
  align-items: center;
}

.layout img {
  object-fit: contain;
  width: 100%;
  height: 100%;
}

div.slide > img:only-child,
div.slide > canvas:only-child,
div.slide > video:only-child {
  width: 100%;
}

/*---------------------------*/

[data-body-class=slidedark][data-absolute]:not([data-absolute=absolute]) h2 {
  background: rgba(0,0,0,0.65);
  padding: 10px 30px;
}

body:not(.print-mode) [data-absolute=absolute] h2,
body:not(.print-mode) [data-absolute=absolute] p {
  position: absolute;
  top: 5vh;
  left: 50%;
  width: auto;
  line-height: 0.6;
  font-size: 7vh;
  text-align: center;
  transform: translateX(-50%);
}

body:not(.print-mode) [data-absolute=absolute] p {
  top: auto;
  bottom: 5vh;
  line-height: 1;
  font-size: 4vh;
}

body:not(.print-mode) [data-body-class=slidedark][data-absolute=absolute] p {
  top: 50vh;
}

.logo {
  display: none;
  position: absolute;
  top: 3vh;
  left: 3vh;
}

body.slidedark .logo.dark,
body:not(.slidedark) .logo:not(.dark) {
  display: block;
}

strong,
p span,
[class^=pyramid-level] span {
  color: #12d700;
}

blockquote {
  font-size: 6em;
  font-weight: 200;
}

blockquote strong {
  display: block;
}

blockquote:before {
  position: absolute;
  content: '\201C';
  margin: -16px 0 0 -80px;
  font: 200px/1 'PT Sans', sans-serif;
  color: #ccc;
}

li {
  margin-bottom: 8vh;
}

.small {
  line-height: 1.25;
  font-size: 40%;
  font-weight: 400;
}

.transparent {
  color: #212121;
  text-shadow: -1px -1px 1px #fff, 1px 1px 1px #000;
}

.bottom {
  position: absolute;
  bottom: 20vh;
  font-size: 5rem;
  transform: translatex(-50%);
}

.grid-container {
  display: flex;
  flex-direction: column;
}

.flex {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  width: 60vw;
}

.flex>* {
  display: flex;
  align-items: center;
  justify-content: center;
  flex: 1;
}

.flex p {
  position: absolute;
  bottom: -9vh;
  font-size: 3vh;
}

.flex svg {
  min-width: 100em;
}
